<template>
  <div class="wrapper">
    <button v-on:click="goto('room')" :class="{ active: currentPage === 'room' }">
      <v-icon>face</v-icon>
    </button>
    <button v-on:click="goto('chat')" :class="{ active: currentPage === 'chat' }">
      <v-icon>chat</v-icon>
    </button>
    <button v-on:click="goto('file')" :class="{ active: currentPage === 'file' }">
    <v-icon>folder</v-icon>
    </button>
  </div>
</template>
<style scoped>
* {
  color: white!important;
}
.wrapper {
  display: none
}
@media screen and (max-width: 480px) {
/* 480pxまでの幅の場合に適応される */
  .wrapper {
    display: block;
    height: 5vh;
    /* background-color: #EC654F */
    background-color: gray;
  }
  button {
    width: 32%;
    border-radius: 3px;
    height: 100%;
    background-color: transparent;
    color: white;
  }
  button.active {
    /* background-color: #DD604C */
    background-color: lightgray
  }
}
</style>
<script>
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  data () {
    return {
    }
  },
  methods: {
    goto: function (dist) {
      this.$store.dispatch(
        'page/goto',
        dist
      )
    }
  }
}
</script>



