<template>
  <div class="wrapper">
    <textarea
        class="message-textarea"
        autofocus
        placeholder="入力してください"
        v-on:input="update"
        :value="value"
      ></textarea>
  </div>
</template>
<style scoped>
.wrapper {
  height: 100%;
  width: 100%;
  position: relative;
  border-radius: 10px;
  padding: 30px 3px 3px 3px;
  border: lightgray solid 1px;
}
.message-textarea {
  text-align: left;
  resize: none;
  overflow: scroll;
  padding: 10px;
  width: 100%;
  background-color: white;
  height: 100%;
}

.message-textarea:focus {
  outline-color: transparent;
  outline-style: none;
}
</style>
<script>
export default {
  props: {
    value: { type: String, required: true }
  },
  methods: {
    update: function (e) {
      this.$emit('input', e.target.value)
    }
  }
}
</script>



