<template>
  <button
    v-on:click="$emit('click', $event.target)"
    v-bind:style="{ 'background-color': bgcolor }"
  >
    <MyButtonLoader
      v-if="loading"
    />
    <span v-else>{{ caption }}</span>
  </button>
</template>
<style scoped>
  button {
    color: white;
    width : 100%;
    border-radius: 5px;
    display: block;
    height: 40px;
    margin : 10px 0;
  }
</style>

<script>
import MyButtonLoader from './MyButtonLoader'

export default {
  name: 'MyButton',
  components: {
    MyButtonLoader
  },
  props: {
    caption: { type: String },
    bgcolor: { type: String },
    loading: { type: Boolean, default: false }
  }
}
</script>



