<template>
  <div
    v-on:click="onRoomClick(room.id)"
    :class="{'wrapper': true, active: room.id === activeRoomId }"
  >
    <div
      class="conversation-row"
    >
      <span class="conversation-name">{{ room.title }}({{ room.members.length }}名)</span>
      <span v-if="!hideMembers" class="hide-members" v-on:click="hideMembers = true">
        <img src="@/assets/images/minus-circle.png">
      </span>
      <span v-if="hideMembers" class="show-members" v-on:click="hideMembers = false">
        <img src="@/assets/images/plus-circle.png">
      </span>

    </div>
    <div v-if="!hideMembers">
      <div
        class="conversation-member"
        v-for="(member, index) in otherMembers"
        :key="index"
      >
        <img v-if="member.user.thumbnail" class="member-thumbnail" :src="hostName + member.user.thumbnail">
        <img v-else class="member-thumbnail" src="@/assets/images/no-image.png">
        <span class="member-name">{{ member.user.name }}</span>
      </div>
    </div>
  </div>
</template>
<style scoped>
.wrapper {
  display: relative;
  padding: 5px;
}
.conversation-row {
  font-size: 12px;
  display:flex;
  position :relative;
  background-color: transparent;
  height: 30px;
  color: white;
}
.active {
  background-color: #DD604C;
  border-left: 3px white solid
}

.conversation-name {
  padding-left: 10px;
  display: inline-block;
  font-size: 14px;
  text-align: left;
  line-height: 30px;
}
.hide-members, .show-members {
  display: inline-block;
  line-height:30px;
  padding: 0 10px;
  cursor: pointer
}
.hide-members > img, .show-members > img {
  width: auto;
  height: 70%;
  margin-top: 15%
}
.conversation-member {
  display: flex;
  padding-left: 20px;
  height: 30px;
}
.member-thumbnail, .member-name {
  display: inline-block;
}
.member-thumbnail {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.member-name {
  padding-left: 10px;
  color: white;
  font-size: 14px;
  line-height: 30px;
}

</style>
<script>
import moment from 'moment'
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  data () {
    return {
      hideMembers: false
    }
  },
  props: {
    room: { type: Object, requred: true }
  },
  computed: {
    otherMembers: function () {
      return this.room.members.filter((member) => {
        return (member.user.id !== this.userInfo.id)
      })
    }
  },
  filters: {
    format: function (value) {
      return moment(value * 1000).format('YYYY/MM/DD HH:mm')
    }
  },
  methods: {
    onRoomClick: function (id) {
      if (id !== this.activeRoomId) {
        this.$store.dispatch('chat/enter', { roomId: id })
      }
    }
  }
}
</script>



