<template>
  <div
    class="file-wrapper"
  >
    <img
      v-if="fileIsImage"
      :src="hostName + fileInfo.file_url"
      class="file-image"
    >
    <img
      v-else
      src="@/assets/images/file.png"
      class="file-image"
    >
    <span
      class="filename"
    >
      <a :href="hostName + fileInfo.file_url">
      {{ fileInfo.file_name | shorten }}
      </a>
    </span>
  </div>
</template>
<style scoped>
  .file-wrapper {
    width: 100px;
    height: auto  ;
    display:inline-block;
    margin: 3px;
  }
  .file-image {
    height: auto;
    width: 100%;
    border-radius: 5px;
    margin:none
  }
  .filename {
    display: block;
    line-height: 12px;
  }
  a {
    color:gray;
    font-weight: bold;
    font-size: 12px;
  }
</style>
<script>
import basemixins from '../mixins/basemixin'
export default {
  mixins: [basemixins],
  props: {
    fileInfo: { type: Object }
  },
  data () {
    return {
    }
  },
  computed: {
    fileName: function () {
      return this.fileInfo.file_name
    },
    fileIsImage: function () {
      let ext = this.fileName.slice((this.fileName.lastIndexOf('.') - 1 >>> 0) + 2)
      var globalRegex = RegExp('jpe?g|png', 'g')
      return globalRegex.test(ext)
    }
  },
  methods: {
    isImage: function (val) {
      var globalRegex = RegExp('jpe?g|png', 'g')
      return globalRegex.test(val)
    }
  },
  filters: {
    shorten: function (val) {
      let maxLength = 20
      if (val.length > maxLength) {
        return val.slice(0, maxLength) + '...'
      } else {
        return val
      }
    }
  }
}
</script>



