export default {
  namespaced: true,
  state: {
    token: '',
    userInfo: null
  },
  mutations: {
    settoken (state, token) {
      state.token = token
    },
    setUserInfo (state, data) {
      state.userInfo = data
    },
    destroy (state) {
      state.token = ''
      state.userInfo = null
    }
  },
  getters: {
    userId: (state, getters) => {
      return state.userInfo.id
    },
    token: (state, getters) => {
      return state.token
    },
    userInfo: (state, getters) => {
      return state.userInfo
    },
    isAuthenticated: (state, getters) => {
      return state.token !== ''
    },
    userInfoGetUrl: (state, getters) => {
      return 'auth/users/' + state.userInfo.id
    },
    userInfoPostUrl: (state, getters) => {
      return getters.userInfoGetUrl + '/'
    },
    passwordChangeUrl: (state, getters) => {
      return 'auth/passwordchange/'
    }
  },
  actions: {
    async getUserInfo ({ commit, getters, dispatch }, data) {
      try {
        let result = await dispatch(
          'http/get',
          { url: getters.userInfoGetUrl },
          { root: true }
        )
        commit('setUserInfo', result.data)
      } catch (err) {
        alert(err)
      }
    },
    async getToken ({ commit, dispatch }, data) {
      try {
        let result = await dispatch(
          'http/post',
          { url: 'auth/gettoken/', data: data },
          { root: true }
        )
        if (result.data.token) {
          commit('settoken', result.data.token)
          commit('setUserInfo', result.data.user)
          console.log(result.data)
        }
      } catch (err) {
        throw err
      }
    },
    async update ({ commit, dispatch, getters }, data) {
      try {
        let result = await dispatch(
          'http/patch',
          { url: getters.userInfoPostUrl, data: data, content_type: 'multipart/form-data' },
          { root: true }
        )
        commit('setUserInfo', result.data)
      } catch (err) {
        throw err
      }
    },
    async changePassword ({ commit, dispatch, getters }, data) {
      try {
        await dispatch(
          'http/post',
          { url: getters.passwordChangeUrl, data: data },
          { root: true }
        )
      } catch (err) {
        throw err
      }
    },
    destroy ({ commit, dispatch }, data) {
      commit('destroy')
    }
  }
}



