<template>
  <div class="base">
    <div class="wrapper">
      <img class="logo" src="../assets/images/logo_2.png">
      <div class="login-card">
        <flash-message class="myCustomClass"></flash-message>
        <form
          ref="form"
        >
          <MyInput
            v-model="form.username"
            type="text"
            placeholder="メールアドレス"
          />
          <div class="spacer"></div>
          <MyInput
            v-model="form.password"
            :type="'password'"
            placeholder="パスワード"
          />
          <MyButton
            :loading = loading
            caption="ログイン"
            bgcolor="#db362e"
            @click="login"
          />
        </form>
      </div>
    </div>
  </div>
</template>
<style scoped>
  .base {
    height: 90vh;
    width:  100vw;
    text-align: center;
    background-color: #f8f9fa
  }
  .wrapper {
    width: 500px;
    display: inline-block;
  }
  .logo {
    margin-top: 2rem;
    height: 100px;
  }
  .login-card {
    width: 100%;
    border-radius: 10px;
    margin: 2rem 0;
    padding: 2rem;
    background-color: white;
    border: 1px solid rgba(0,0,0,.125)
  }
  .forget-password {
    text-align: left
  }
  p {
    margin: 10px 0;
  }
  .spacer {
    height: 10px;
  }
</style>
<script>
import MyInput from './components/MyInput'
import MyButton from './components/MyButton'
export default {
  components: {
    MyInput,
    MyButton
  },
  data () {
    return {
      loading: false,
      form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    async login () {
      this.loading = true
      try {
        await this.$store.dispatch('auth/getToken', this.form)
        await this.$store.dispatch('auth/getUserInfo', null)
        // login successfull
        this.$router.push('/chat')
      } catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = 'ログインに失敗しました'
        } else {
          message = '予期しないエラーが起きました' + data
        }
        this.flash(message, 'error', {
          timeout: 3000
        })
      }
      this.loading = false
    }
  },
  mounted () {
    this.$store.dispatch(
      'auth/destroy'
    )
  }
}
</script>



