<template>
<div>
  <Navbar />
  <div class="my-container" ref="container">
    <flash-message class="myCustomClass"></flash-message>

    <Modal
      v-show="modal"
      :isError="true"
    >
      <template
        v-slot:body
      >
       <p>エラーがあります</p>
       <v-btn v-on:click="modal = false">
         閉じる
       </v-btn>
      </template>
    </Modal>
    <div class="my-card">
      <h2>パスワード変更</h2>
      <hr>
      <div
        ref="form"
      >
        <div class="tags">
          <label for="old_password">現在のパスワード</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          ref="old_password"
          id="old_password"
          v-model="form.old_password"
          type="password"
        />
        <p class="error">{{ errors.old_password }}</p>
        <div class="tags">
          <label for="new_password_1">新しいパスワード</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="new_password_1"
          v-model="form.new_password_1"
          type="password"
        />
        <p class="error" v-if="errors.new_password_1">{{ errors.new_password_1 }}</p>

        <div class="tags">
          <label for="new_password_2">新しいパスワード(確認)</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="new_password_2"
          v-model="form.new_password_2"
          type="password"
        />
        <p class="error" v-if="errors.new_password_2">{{ errors.new_password_2 }}</p>
        <div class="spacer"></div>
        <MyButton
          caption="更新"
          bgcolor="#db362e"
          @click="post"
          :loading = buttonLoading
        />
      </div>
    </div>
  </div>
</div>
</template>
<style scoped>
  label {
    color: gray;
  }
  .my-container {
    padding: 10px 0;
    text-align: center;
    width: 100vw;
    height: 100vh;
    background-color: #efefef
  }
  .logo {
    width: 100%;
  }
  .logo img {
    height: 100px;
  }
  .my-card {
    display: inline-block;
    width: 400px;
    border-radius: 10px;
    padding: 2rem;
    background-color: white;
    border: 1px solid rgba(0,0,0,.125)
  }
  .tags {
    text-align: left;
    margin-top: 20px;
  }
  .required {
    display: inline-block;
    padding: 2px;
    color: white;
    font-weight: bold;
    font-size: 12px;
    background-color: red;
    border-radius: 3px;
  }
  .spacer {
    height: 10px;
  }
  p.error {
    background-color: red;
    color: white;
    border-radius: 3px;
    text-align: left;
    padding: 0 5px;
  }
  @media screen and (max-width: 480px) {
  /* 480pxまでの幅の場合に適応される */
    .my-card {
      width: 350px
    }
  }
</style>
<script>
import Navbar from './Navbar'
import Modal from './components/Modal'
import MyInput from './components/MyInput'
import MyButton from './components/MyButton'
import basemixin from './mixins/basemixin'
export default {
  mixins: [basemixin],
  components: {
    Navbar,
    Modal,
    MyInput,
    MyButton
  },
  data () {
    return {
      buttonLoading: false,
      form: {
        old_password: '',
        new_password_1: '',
        new_password_2: ''
      },
      errors: {},
      modal: false
    }
  },
  watch: {
    'form.old_password': function (val) {
      this.errors.old_password = ''
    },
    'form.new_password_1': function (val) {
      this.errors.new_password_1 = ''
    },
    'form.new_password_2': function (val) {
      this.errors.new_password_2 = ''
    }
  },
  methods: {
    initializeErrors: function () {
      this.errors = {
        old_password: '',
        new_password_1: '',
        new_password_2: ''
      }
    },
    validate: function () {
      if (!this.form.old_password || this.form.old_password === '') {
        this.errors.old_password = 'この項目は必須です'
        return false
      }
      if (!this.form.new_password_1 || this.form.new_password_1 === '') {
        this.errors.new_password_1 = 'この項目は必須です'
        return false
      }
      if (!this.form.new_password_2 || this.form.new_password_2 === '') {
        this.errors.new_password_2 = 'この項目は必須です'
        return false
      }
      if (this.form.new_password_1 !== this.form.new_password_2) {
        this.errors.new_password_1 = '確認用パスワードが一致しません'
        return false
      }
      return true
    },
    async post () {
      this.initializeErrors()
      if (!this.validate()) {
        window.scrollTo(0, 0)
        this.modal = true
        return false
      }
      this.buttonLoading = true
      try {
        await this.$store.dispatch(
          'auth/changePassword',
          this.form
        )
        window.scrollTo(0, 0)
        let message = '更新が完了しました。'
        this.form.old_password = ''
        this.form.new_password_1 = ''
        this.form.new_password_2 = ''

        this.flash(message, 'success', {
          timeout: 3000
        })
      } catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = '更新に失敗しました'
          console.log(data)
        } else {
          message = '予期しないエラーが起きました' + data
          console.log(message)
        }
        this.flash(message, 'error', {
          timeout: 3000
        })
      }
      this.buttonLoading = false
      this.isModified = false
    },
    preventRefresh (event) {
      event.preventDefault()
      event.returnValue = ''
    }
  },
  created () {
    this.initializeErrors()
  }
}
</script>



