<template>
  <div class="input-area">
    <MainSectionInputAreaFileThumbnail
      v-for="(file, id) in data.files"
      :key="id"
      :file="file"
      :count="id"
      v-on:delete="deleteFile(id)"
    />
    <div class="text-div">
      <MyTextArea
        v-model="data.messagetext"
      />
      <button
        class="send-btn"
        v-on:click="sendMessage"
      >
        送信
      </button>
      <MyFileInput
        v-model="data.files"
      />
    </div>
    <Modal
        v-show="modal.show"
        :isError="false"
      >
        <template
          v-slot:body
        >
          <p>
            {{ modal.message }}
          </p>
        <button
          class="modal-close-button"
          v-on:click="modal.show = false"
        >
          閉じる
        </button>
        </template>
      </Modal>
  </div>
</template>
<style scoped>
    /* 入力部分 */
    .input-area {
      width: 100%;
      height: 25%;
      display: flex;
      background-color: white;
      padding: 3px 10px;
    }
    .text-div {
      width: 100%;
      margin-bottom: 10px;
      position: relative;;
    }
    .send-btn {
      color: white;
      position:absolute;
      top: 5px;
      font-size: 12px;
      padding: 3px 10px;
      border-radius: 3px;
      background-color:#EC654F;
      right: 10px;
    }
    .attach-file-div {
      width: 10%
    }
    .modal-close-button {
      background-color: red;
      color: white;
      padding: 4px;
      border-radius: 5px;
    }
</style>
<script>
import Modal from './Modal'
import MyTextArea from './MyTextArea'
import MyFileInput from './MyFileInput'
import MainSectionInputAreaFileThumbnail from './MainSectionInputAreaFileThumbnail'
export default {
  data () {
    return {
      modal: {
        show: false,
        message: ''
      },
      data: {
        messagetext: '',
        files: []
      }
    }
  },
  computed: {
    filecount: function () {
      return this.data.files.length
    }
  },
  watch: {
    'data.files': function (data) {
      console.log(data)
    }
  },
  methods: {
    getJsonData: async function (idList) {
      return {
        'command': 'send',
        'room': this.$store.state.chat.roomId,
        'message': this.data.messagetext,
        'attachment': idList
      }
    },
    deleteFile: function (id) {
      this.data.files.splice(id, 1)
    },
    sendMessage: async function () {
      if (this.data.messagetext === '') {
        this.modal.message = 'メッセージを入力してください'
        this.modal.show = true
        return false
      }
      let uploadedFiles = await this.uploadFile(this.data.files)
      let idList = []
      for (let i = 0; i < uploadedFiles.length; i++) {
        idList.push(uploadedFiles[i].id)
      }
      this.$store.dispatch('sendMessage', { message: this.data.messagetext, attachment: idList })
      this.data.messagetext = ''
      this.data.files = []
    },
    uploadFile: async function (files) {
      try {
        let formData = new FormData()
        for (let i = 0; i < files.length; i++) {
          formData.append(files[i].name, files[i])
        }
        let data = await this.$store.dispatch(
          'http/post',
          { url: 'chat/upload/', data: formData, contentType: 'multipart/form-data' }
        )
        return data.data
      } catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = 'ログインに失敗しました'
          console.log(data)
        } else {
          message = '予期しないエラーが起きました' + data
        }
        this.flash(message, 'error', {
          timeout: 3000
        })
      }
    },
    toBase64: async function (file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = () => resolve(reader.result)
        reader.onerror = error => reject(error)
      })
    }
  },
  components: {
    MyFileInput,
    MyTextArea,
    MainSectionInputAreaFileThumbnail,
    Modal
  }
}
</script>



