<template>
  <div class="topnav">
    <div class="logo">
      <img src="@/assets/images/logo_2.png">
      <span class="logo-text"><strong>Opukita</strong> renraku navi</span>
    </div>
    <div class="links">
      <router-link tag="button" to="/chat">
        <img class="gear" src="@/assets/images/chat-processing.png">
      </router-link>
      <router-link tag="button" to="/profile">
        <img class="gear" src="@/assets/images/gear.png">
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    routes: { type: Array, require: true },
    value: { type: String, require: true }
  },
  data () {
    return {
    }
  }
}
</script>
<style scoped>
 div.topnav {
    height: 10vh;
    width:100vw;
    background-color: #373c38;
    box-shadow: 1px 1px 1px gray;
    z-index: 1000;
    display: flex;
    vertical-align: middle
  }
  .to-top {
    color: white
  }
  .topnav .logo {
    display: flex;
    width: 70%;
    text-align: left;
    height: auto;
    margin:10px 10px 10px 20px;

  }
  .logo-text {
    color: white;
    display: inline-block;
    line-height: 8vh;
    font-size: 20px;
    padding-left: 10px;
  }
  img {
    height: 100%;
  }
  .gear {
    height: 40%;
    margin-top: 5px;
  }
  .topnav .links {
    width:50%;
    display: inline-block;

  }
  button {
    height: 100%;
    width: 100px;
    background-color: transparent;
    color: white;
    font-size: 16px;
    outline: none
  }
  @media screen and (max-width: 480px) {
    .topnav .logo {
      width: 50%;
    }
    .links {
      width: 40%;
    }
    button {
      width: 30%;
    }
    .gear {
      height: 40%;
      margin-top: 5px;
    }
    img {
     max-width: 100%;
    }
    .logo-text {
      display: none
    }
  }
</style>



