<template>
  <div>
    <Navbar />
    <MobileNavBar />
    <div class="content">
      <RoomsSection />
      <MainSection />
      <FileCabinetSection />
      <Modal
        v-show="socket.isConnecting || socket.isReconnecting"
        :isError="false"
      >
        <template
          v-slot:body
        >
          <p
            v-if="socket.isReconnecting"
          >
            再接続中....
          </p>
          <p
            v-if="socket.isConnecting"
          >
            接続中....
          </p>
        </template>
      </Modal>
    </div>
  </div>
</template>
<style scoped>
  div.content {
    height: 90vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
  }
  @media screen and (max-width: 480px) {
  /* 480pxまでの幅の場合に適応される */
    div.content {
      height: 85vh!important
    }
  }
</style>
<script>
import Navbar from './Navbar'
import RoomsSection from './components/RoomsSection'
import MainSection from './components/MainSection'
import Modal from './components/Modal'
import FileCabinetSection from './components/FileCabinetSection'
import MobileNavBar from './components/MobileNavBar'
import basemixin from './mixins/basemixin'
export default {
  mixins: [basemixin],
  components: {
    RoomsSection,
    MainSection,
    Navbar,
    Modal,
    FileCabinetSection,
    MobileNavBar
  },
  methods: {
    refreshHook: async function () {
      alert('are you leaving?')
      await this.$store.dispatch('disconnect')
    }
  },
  beforeMount () {
    window.addEventListener('beforeunload', this.refreshHook)
    this.$once('hook:beforeDestroy', () => {
      window.removeEventListener('beforeunload', this.refreshHook)
    })
  },
  async mounted () {
      await this.$store.dispatch('connect')
  },
  async beforeDestroy () {
    await this.$store.dispatch('disconnect')
  }
}
</script>



