<template>
  <div id="app">
    <vue-loaders v-show="loading" name="ball-rotate" color="red" scale="1"></vue-loaders>
    <router-view
      v-show="!loading"
      v-on:loading="setLoading"
    />
  </div>
</template>
<script>
export default {
  name: 'App',
  data () {
    return {
      loading: false,
      activeRoute: ''
    }
  },
  computed: {
    publicRoutes: function () {
      return this.$router.options.routes.filter(route => route.meta.isPublic)
    },
    privateRoutes: function () {
      return this.$router.options.routes.filter(route => !route.meta.isPublic)
    }
  },
  watch: {
    $route: function (to, from) {
      this.activeRoute = to.name
    }
  },
  methods: {
    setLoading (val) {
      this.loading = val
    },
    setProcessing (val) {
      this.processing = val
    }
  }
}
</script>
<style>
@import './assets/css/loading.css';
@import './assets/css/processing.css';
#app {
  font-family: 'Yu Gothic';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  display: flex;
  margin: 0;
  flex-direction: column;
}
</style>



