<template>
  <div class="wrapper">
    <input
      type="file"
      style="display: none;"
      ref="image"
      v-on:input="onFileChange"
      mulitple="multiple"
    >
    <button
      v-on:click="pickImage"
    >
      <v-icon>attach_file</v-icon>
    </button>
  </div>
</template>
<style scoped>
    .wrapper {
      position: absolute;
      top:5px;
      left: 5px;
      display: inline-block;
    }

    .attach-file-btn {
      text-align: center;
      display: inline-block;
      vertical-align: top;
      background-color: teal;
      color: white;
      width: 50px;
      height: 50px;
      border-radius: 25px;
      border: none;
    }
</style>
<script>
import MainSectionInputAreaFileThumbnail from './MainSectionInputAreaFileThumbnail'
let fileMaxSize = 2000000
let fileMaxCount = 3
export default {
  props: {
    value: { type: Array }
  },
  components: {
    MainSectionInputAreaFileThumbnail
  },
  data () {
    return {
      files: [],
      isError: false,
      errorMessage: ''
    }
  },
  watch: {
    'value': function (val) {
      this.files = val
      if (val.length === 0) {
        this.$refs.image.value = ''
      }
    }
  },
  mounted () {
    this.files = this.value
  },
  methods: {
    // ファイル選択を表示
    pickImage () {
      this.$refs.image.click()
    },
    // ファイルアップが完了したら動作
    onFileChange (e) {
      let files = e.target.files || e.dataTransfer.files
      // ファイルサイズ
      if (files[0].size > fileMaxSize) {
        this.isError = true
        this.errorMessage = 'ファイルサイズが大きすぎます。'
        return
      }
      // ファイル枚数
      if (this.files.length + 1 > fileMaxCount) {
        this.isError = true
        this.errorMessage = 'アップロードできるファイルは最大' + fileMaxCount + 'ファイルです。'
        return
      }
      this.files.push(files[0])
      this.$emit('input', this.files)
      this.$emit('change', this.files)
    }
  }
}
</script>



