<template>
  <div
    :class="'attachment-item count' + count"
  >
    <div class="wrapper">
      <div class="img-wrapper">
        <img
          v-if="file.type.includes('image')"
          :src="fileurl"
        >
        <img
          v-else
          src = "@/assets/images/file.png"
        >
      </div>
      <p>
        {{ file.name }}
      </p>
      <span
        class="delete-attachment"
        fab
        v-on:click="deleteAttachment(count)"
      >
        ✖︎
      </span>
    </div>
  </div>
</template>
<style scoped>
  .attachment-item {
    position: absolute;
    right: calc(30vw + 20px);
    z-index: 999;
    height: 40px;
    width: 400px;
    border: 1px solid rgba(0,0,0,.125);
    background-color: white;
    -webkit-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
    -moz-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
    box-sizing:content-box;
    transition: all 1s;
  }
  .wrapper {
    position:relative;
    font-size:12px;
    text-align: left;
    height: 100%;
  }
  .wrapper > p {
    display  : inline-block;
    width: 60%;
    vertical-align: top;
    line-height: 40px;
    font-size: 14px;
  }
  .img-wrapper {
    height: 100%;
    width: 30%;
    display: inline-block
  }
  img {
    width: auto;
    height: 100%
  }
  .text-wrapper {
    font-size: 12px;
    display: inline-block;
    width: 40%;
    height: 100%;
    margin-top: 0px
  }
  .count0 {
    top: calc(75vh - 55px);
  }
  .count1 {
    top: calc(75vh - 110px);
  }
  .count2 {
    top: calc(75vh + 165px);
  }

  .delete-attachment {
    position: absolute;
    top: -10px;
    right: -10px;
    background-color:red!important;
    color: white;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
    float:right;
    height: 30px;
    width: 30px;
  }
  @media screen and (max-width: 480px) {
    .attachment-item {
      position: absolute;
      right: calc(20px);
      z-index: 999;
      height: 40px;
      width: 300px;
      border: 1px solid rgba(0,0,0,.125);
      background-color: white;
      -webkit-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
      -moz-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
      box-sizing:content-box;
      transition: all 1s;
    }
    p {
      font-size: 12px;
    }
  }
</style>
<script>
export default {
  data () {
    return {
      fileurl: ''
    }
  },
  props: {
    file: { type: File, required: true },
    count: { type: Number, required: true }
  },
  created () {
    this.createThumbnail()
  },
  watch: {
    'file': function (file) {
      this.createThumbnail()
    }
  },
  methods: {
    createThumbnail: function () {
      const data = URL.createObjectURL(this.file)
      this.fileurl = data
    },
    deleteAttachment: function (id) {
      this.$emit('delete', id)
    }
  }
}
</script>



