<template>
  <div class="wrapper">
    <input
      type="file"
      style="display: none;"
      ref="image"
      v-on:input="onFileChange"
    >
    <button
      v-on:click="pickImage"
      class="attach-file-btn"
    >
      <v-icon class="icon">attach_file</v-icon>
    </button>
    <input
      class="file-name"
      type="text"
      :value="filename"
      readonly
      v-on:click="pickImage"
    >
    <div class="preview">
      <img v-if="value" :src="fileurl">
    </div>
  </div>
</template>
<style scoped>
    .wrapper {
      margin: 0;
      display: flex;;
      height: 40px;
      line-height: 40px;
    }
    .icon {
      color:gray
    }
    .attach-file-btn {
      text-align: center;
      display: inline-block;
      border-radius: 3px;
      width: 10%;
      height: 100%;
    }
    .file-name {
      width: 90%;
      border: lightgray solid 1px;
      height: 100%;
      border-radius: 5px;
      padding: 0 10px;
    }
    .preview {
      width: 10%;
      display: inline-block;
      height:100%;
      padding: 3px;
      vertical-align: middle
    }
    .preview img {
      max-width: 100%;
      max-height: 100%;
    }
    :focus {
      outline-color: red;
      outline-style: 1px solid
    }
</style>
<script>
let fileMaxSize = 2000000
// let fileMaxCount = 3
export default {
  props: {
    value: { type: File }
  },
  components: {
  },
  watch: {
    value: function (val) {
      // this.$forceUpdate()
    }
  },
  computed: {
    filename: function () {
      if (this.value) {
        return this.value.name
      } else {
        return ''
      }
    },
    fileurl: function () {
      if (this.value) {
        return URL.createObjectURL(this.value)
      }
    }
  },
  methods: {
    // ファイル選択を表示
    pickImage () {
      this.$refs.image.click()
    },
    // ファイルアップが完了したら動作
    onFileChange (e) {
      let file = e.target.files[0]
      // ファイルサイズ
      if (file.size > fileMaxSize) {
        this.isError = true
        this.errorMessage = 'ファイルサイズが大きすぎます。'
        return
      }
      this.$emit('input', file)
      this.$emit('change', file)
    }
  },
  updated () {
  }
}
</script>



