export default {
  methods: {
  },
  computed: {
    socket: function () {
      return this.$store.getters['chat/socket']
    },
    loading: function () {
      return this.$store.getters['chat/loading']
    },
    rooms: function () {
      return this.$store.getters['chat/rooms']
    },
    messages: function () {
      return this.$store.getters['chat/messages']
    },
    activeRoomId: function () {
      return this.$store.getters['chat/activeRoomId']
    },
    userInfo: function () {
      return this.$store.getters['auth/userInfo']
    },
    hostName: function () {
      return process.env.HOSTNAME
    },
    files: function () {
      return this.$store.getters['chat/files']
    },
    currentPage: function () {
      return this.$store.getters['page/activePage']
    }
  }
}



