import Vue from 'vue'
import Vuex from 'vuex'

import createPersistedState from 'vuex-persistedstate'

import auth from '@/store/modules/auth'
import http from '@/store/modules/http'
import chat from '@/store/modules/chat'
import page from '@/store/modules/page'
Vue.use(Vuex)

const COMMAND_TYPES = {
  SEND: 'send',
  SUBSCRIBE: 'subscribe',
  UNSUBSCRIBE: 'unsubscribe',
  ENTER: 'enter',
  LEAVE: 'leave'
}
const MESSAGE_TYPES = {
  MSG_TYPE_MESSAGE: 0, // For standard messages
  MSG_TYPE_WARNING: 1, // For yellow messages
  MSG_TYPE_ALERT: 2, // For red & dangerous alerts
  MSG_TYPE_MUTED: 3, // For just OK information that doesn't bother users
  MSG_TYPE_ENTER: 4, // For just OK information that doesn't bother users
  MSG_TYPE_LEAVE: 5, // For just OK information that doesn't bother users
  MSG_TYPE_SUBSCRIBE: 6,
  MSG_TYPE_UNSUBSCRIBE: 7
}

const socketState = {
  connectedAs: null,
  socket: {
    socket: null,
    isConnected: false,
    isReconnecting: false,
    isConnecting: true,
    connectError: false
  },
  rooms: {
    loading: false,
    activeId: null,
    data: []
  },
  messages: {
    loading: false,
    page: 0,
    data: []
  },
  files: {
    loading: false,
    data: []
  }
}
const chatMutations = {
  DISCONNECT (state, data) {
    state.socket.socket = null
    state.rooms.activeId = null
    state.rooms.data = []
    state.messages.page = 0
    state.messages.data = []
    state.files.data = []
  },
  CONNECTED_AS (state, data) {
    state.connectedAs = data
  },
  SOCKET_CONNECTING (state, event) {
    state.isConnecting = true
    state.connectError = false
  },
  SOCKET_ONOPEN (state, event) {
    state.socket.connectError = false
    state.socket.socket = this._vm.$socket
    state.socket.isConnecting = false
    state.socket.isReconnecting = false
    state.socket.isConnected = true
  },
  SOCKET_ONCLOSE (state, event) {
    state.socket.isConnected = false
  },
  SOCKET_ONERROR (state, event) {
    state.socket.isConnecting = false
    state.socket.isReconnecting = false
    state.socket.connectError = true

    console.error(state, event)
  },
  // default handler called for all methods
  SOCKET_ONMESSAGE (state, message) {
    let data = JSON.parse(message.data)
    let room = data.room
    let sender = data.user
    let targetRoom = state.rooms.data.find(item => item.id === room)
    let msgType = data.msg_type
    let msg
    if ('message' in data) {
      msg = JSON.parse(data.message)
      console.log(msg)
    }
    if (msgType === MESSAGE_TYPES.MSG_TYPE_SUBSCRIBE) {
      if (sender === state.connectedAs) {
        return false
      }
      // targetRoom.user.is_online = true
    } else if (msgType === MESSAGE_TYPES.MSG_TYPE_UNSUBSCRIBE) {
      if (sender === state.connectedAs) {
        return false
      }
      // targetRoom.user.is_online = false
    } else if (msgType === MESSAGE_TYPES.MSG_TYPE_ENTER) {
      if (sender === state.connectedAs) {
        return false
      }
      // 入室
      // targetRoom.opponent_is_reading = true
      // 自身のメッセージを既読にする。
      targetRoom.messages.forEach(element => {
        element.is_read = true
      })
    } else if (msgType === MESSAGE_TYPES.MSG_TYPE_LEAVE) {
      if (sender === state.connectedAs) {
        return false
      }
      targetRoom.opponent_is_reading = false
      console.log('someone left the room')
    } else if (msgType === MESSAGE_TYPES.MSG_TYPE_MESSAGE) {
      if (targetRoom.id !== state.rooms.activeId) {
        targetRoom.new_messages += 1
      }
      state.messages.data.push(msg)
      const targetIndex = state.rooms.data.findIndex(item => item.id === targetRoom.id)
      state.rooms.data.push(...state.rooms.data.splice(0, targetIndex))

      for (let j = 0; j < msg.attachments.length; j++) {
        state.files.data.push(msg.attachments[j])
      }
    }
  },
  // mutations for reconnect methods
  SOCKET_RECONNECT (state, count) {
    state.socket.connectError = false
    state.socket.isReconnecting = true
    if (count > 3) {
      alert('hoge')
    }
  },
  SOCKET_RECONNECT_ERROR (state) {
    state.socket.connectError = true
  },
  SET_FILES (state, data) {
    state.files.loading = false
    state.files.data = data.files
  },
  PUSH_MESSAGES (state, data) {
    if (data.messages.length === 0) {
      state.messages.noMore = true
      return
    }
    state.messages.loading = false
    state.messages.page += 1
    state.messages.data = data.messages.concat(state.messages.data)
  },
  SUBSCRIBE_ROOM (state, data) {
    let jsonData = {
      command: COMMAND_TYPES.SUBSCRIBE,
      room: data.id
    }
    state.socket.socket.send(JSON.stringify(jsonData))
    state.rooms.data.push(data)
  },
  ENTER_ROOM (state, data) {
    let jsonData = {
      command: COMMAND_TYPES.ENTER,
      room: data.roomId
    }
    state.socket.socket.send(JSON.stringify(jsonData))
    state.rooms.activeId = data.roomId
  },
  LEAVE_ROOM (state, data) {
    let jsonData = {
      command: COMMAND_TYPES.LEAVE,
      room: data.roomId
    }
    state.socket.socket.send(JSON.stringify(jsonData))
  }
}
const chatActions = {
  connect ({ commit, dispatch }, data) {
    commit('SOCKET_CONNECTING', {})
    this._vm.$connect(process.env.WSHOST + '/ws/?token=' + this.getters['auth/token'])
    commit('CONNECTED_AS', this.getters['auth/userId'])
    console.log('connect called')
  },
  disconnect ({ commit, dispatch }, data) {
    this._vm.$disconnect()
    commit('DISCONNECT', {})
  },
  async getRooms ({ commit, dispatch }, data) {
    console.log('getRooms called')
    this.state.rooms.loading = true
    this.state.rooms.data = []
    let url = 'chat/chatrooms/'
    try {
      let result = await dispatch('http/get', { url: url })
      for (let i = 0; i < result.data.length; i++) {
        commit('SUBSCRIBE_ROOM', result.data[i])
      }
    } catch (err) {
      alert('err' + err)
    }
    this.state.rooms.loading = false
  },
  async getRoomFiles ({ commit, dispatch }, data) {
    if (isNaN(this.state.rooms.activeId)) {
      this.state.files.loading = false
      return false
    }
    this.state.files.loading = true
    let url = 'chat/upload/?room=' + this.state.rooms.activeId
    try {
      let result = await dispatch(
        'http/get',
        {
          url: url
        }
      )
      commit('SET_FILES', { files: result.data })
    } catch (err) {
      alert('err' + err)
    }
  },
  clearCurrentMessages () {
    this.state.messages.data = []
    this.state.messages.page = 0
    this.state.messages.noMore = false
  },
  async getNextRoomMessages ({ commit, dispatch }, data) {
    if (isNaN(this.state.rooms.activeId)) {
      this.state.files.loading = false
      return false
    }
    if (this.state.messages.noMore) {
      return false
    }
    this.state.messages.loading = true
    const nextPage = this.state.messages.page + 1
    let url = 'chat/chatmessages/?room=' + this.state.rooms.activeId + '&page=' + nextPage
    try {
      let result = await dispatch(
        'http/get',
        {
          url: url
        }
      )
      commit('PUSH_MESSAGES', { messages: result.data })
    } catch (err) {
      alert('err' + err)
    }
  },
  subscribe ({ commit, dispatch }, data) {
    commit('SUBSCRIBE_ROOM', data)
  },
  enter ({ commit, dispatch }, data) {
    commit('ENTER_ROOM', data)
  },
  leave ({ commit, dispatch }, data) {
    commit('LEAVE_ROOM', data)
  },
  sendMessage ({ commit, dispatch }, data) {
    data.command = COMMAND_TYPES.SEND
    data.room = this.state.rooms.activeId
    this.state.socket.socket.send(JSON.stringify(data))
  }
}
export default new Vuex.Store({
  modules: {
    auth,
    http,
    chat,
    page
  },
  actions: chatActions,
  state: socketState,
  mutations: chatMutations,
  plugins: [createPersistedState({
    key: 'myState',
    storage: window.sessionStorage
  })]
})



