<template>
  <div
    class="wrapper"
    ref="container"
  >
    <p
      v-if="socket.connectError"
      class="error"
    >
      接続エラー
    </p>
    <p
      v-if="!activeRoomId"
    >
      会話を選んでください
    </p>
    <h2
      v-else
    >
      {{ activeRoom.room.title }}
    </h2>
  </div>
</template>
<style scoped>
  .wrapper {
    height: 5vh;
    background-color: gray;
    color: white;
  }
  .error {
    background-color: red
  }
</style>
<script>
import MainSectionMessageAreaFileThumbnail from './MainSectionMessageAreaFileThumbnail'
import moment from 'moment'
export default {
  props: {
  },
  components: {
    MainSectionMessageAreaFileThumbnail
  },
  computed: {
    loading: function () {
      return this.$store.getters['chat/loading']
    },
    socket: function () {
      return this.$store.getters['chat/socket']
    },
    rooms: function () {
      return this.$store.getters['chat/rooms']
    },
    messages: function () {
      return this.$store.getters['chat/messages']
    },
    activeRoomId: function () {
      return this.$store.getters['chat/activeRoomId']
    },
    activeRoom: function () {
      console.log(this.rooms)
      return this.rooms.find(item => item.room.id === this.activeRoomId)
    },
    myUserId: function () {
      return this.$store.state.auth.userid
    },
    hostName: function () {
      return process.env.HOSTNAME
    }
  },
  watch: {
    'activeRoomId': function (val) {
    }
  },
  filters: {
    toKidoku: function (bool) {
      if (bool) {
        return '既読'
      } else {
        return '未読'
      }
    },
    formatDateTime: function (val) {
      return moment(val).format('M/D H:MM')
    }
  },
  methods: {
  }
}
</script>



