<template>
  <input
    autocomplete="off"
    ref="input"
    :type="type"
    :placeholder="placeholder"
    v-bind:value="value"
    v-on:input="$emit('input', $event.target.value)"
  />
</template>
<style scoped>
  input {
    width: 100%;
    height: 40px;
    border: solid lightgray 1px;
    border-radius:5px;
    padding: 3px 10px;
    margin: 1px 0;
  }
  :focus {
    outline-color: red;
    outline-style: dashed 1px
  }
</style>
<script>
export default {
  name: 'MyInput',
  props: {
    focused: { type: Boolean },
    value: { type: String },
    type: { type: String, requre: true },
    placeholder: { type: String }
  },
  updated () {
    if (this.focused) {
      alert('hoge')
      this.$refs.input.focus()
    }
  }
}
</script>



