export default {
  namespaced: true,
  actions: {
    goto ({ commit, dispatch }, data) {
      commit('navigate', data)
    }
  },
  state: {
    activePage: 'chat'
  },
  mutations: {
    navigate (state, data) {
      state.activePage = data
    }
  },
  getters: {
    activePage: (state, getters, rootState, rootGetters) => {
      return state.activePage
    }
  }
}



