<template>
  <div
    :class="{ 'main-panel': true, active: currentPage === 'chat'}"
  >
    <span class="no-room-specified" v-if="isNaN(activeRoomId)">トークルームを選択してください</span>
    <MainSectionMessageArea v-if="!isNaN(activeRoomId)" />
    <MainSectionInputArea v-if="!isNaN(activeRoomId)" />
  </div>
</template>
<style scoped>
  /* main panel */
  div.main-panel {
    width: 50vw;
    height: 100%;
    background-color:  #e6f2ff;
    box-shadow: 1px 1px 1px rgba(0,0,0,.125);
    z-index: 1
  }
  .no-room-specified {
    color: gray;
    font-size: 14px
  }
  @media screen and (max-width: 480px) {
    div.main-panel {
      display: none
    }
    div.main-panel.active {
      width: 100%;
      display: block;
    }
  }
</style>
<script>
import MainSectionRoomTitle from './MainSectionRoomTitle'
import MainSectionMessageArea from './MainSectionMessageArea'
import MainSectionInputArea from './MainSectionInputArea'
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  components: {
    MainSectionRoomTitle,
    MainSectionMessageArea,
    MainSectionInputArea
  },
  props: {
  }
}
</script>



