<template>
  <div class="birthday-wrapper">
    <div class="birthday year">
      <MySelect
        :value="year"
        v-on:change="updateYear"
        :options="years"
      >
        <template slot-scope="props">
          <option v-for="(option, key) in props.options"
              :key="key"
              :value="option.id"
              :selected="year === option.id"
          >{{ option.value }}
          </option>
        </template>
      </MySelect>
    </div>
    <span>年</span>
    <div class="birthday month">
      <MySelect
        :value="month"
        v-on:change="updateMonth"
        :options="months"
        :width = 50
      >
        <template slot-scope="props">
          <option v-for="(option, key) in props.options"
              :key="key"
              :value="option.id"
              :selected="month === option.id"
          >{{ option.value }}
          </option>
        </template>
      </MySelect>
    </div>
    <span>月</span>
    <div class="birthday day">
      <MySelect
        :value="day"
        v-on:change="updateDay"
        :options="days"
        :width = 50
      >
        <template slot-scope="props">
          <option v-for="(option, key) in props.options"
              :key="key"
              :value="option.id"
              :selected="day === option.id"
          >{{ option.value }}
          </option>
        </template>
      </MySelect>
    </div>
    <span>日</span>
  </div>
</template>
<style scoped>
  span {
    color: gray
  }
  .birthday-wrapper {
    display: flex;
    margin-top: 1px;
    line-height: 40px;
  }
  .birthday.year {
    width: 30%;
  }
  .birthday.month {
    width: 20%;
  }
  .birthday.day {
    width: 20%;
  }
</style>
<script>
import MySelect from './MySelect'
export default {
  components: {
    MySelect
  },
  props: {
    value: { type: String }
  },
  computed: {
    year: function () {
      var arr = this.value.split('-')
      return parseInt(arr[0])
    },
    years: function () {
      let arr = []
      let startYear = 1920
      let lastYear = 2020
      for (let i = startYear; i <= lastYear; i++) {
        arr.push({ id: i, value: i })
      }
      return arr
    },
    month: function () {
      var arr = this.value.split('-')
      return parseInt(arr[1])
    },
    months: function () {
      let arr = []
      let startMonth = 1
      let lastMonth = 12
      for (let i = startMonth; i <= lastMonth; i++) {
        arr.push({ id: i, value: i })
      }
      return arr
    },
    day: function () {
      var arr = this.value.split('-')
      return parseInt(arr[2])
    },
    days: function () {
      let arr = []
      let startDay = 1
      let lastDay = 31
      for (let i = startDay; i <= lastDay; i++) {
        arr.push({ id: i, value: i })
      }
      return arr
    }
  },
  methods: {
    updateYear: function (val) {
      this.$emit('input', val + '-' + this.month + '-' + this.day)
    },
    updateMonth: function (val) {
      this.$emit('input', this.year + '-' + val + '-' + this.day)
    },
    updateDay: function (val) {
      this.$emit('input', this.year + '-' + this.month + '-' + val)
    }
  }
}
</script>



