<template>
<div>
  <Navbar />
  <div class="my-container" ref="container">
    <flash-message class="myCustomClass"></flash-message>

    <Modal
      v-show="modal"
      :isError="true"
    >
      <template
        v-slot:body
      >
       <p>エラーがあります</p>
       <v-btn v-on:click="modal = false">
         閉じる
       </v-btn>
      </template>
    </Modal>
    <div class="my-card">
      <h2>登録情報修正</h2>
      <hr>
      <div
        ref="form"
      >
        <div class="tags">
          <label for="name">名前</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          ref="name"
          id="name"
          v-model="form.name"
          type="text"
          placeholder="例) オプト 太郎"
        />
        <p class="error">{{ errors.name }}</p>
        <div class="tags">
          <label for="furigana">名前(カナ)</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="furigana"
          v-model="form.furigana"
          type="text"
          placeholder="例) オプト タロウ"
        />
        <p class="error" v-if="errors.furigana">{{ errors.furigana }}</p>

        <div class="tags">
          <label for="name">生年月日</label>
          <span class="required">必須</span>
        </div>
        <UserProfileBirthDay
          v-model="form.birthday"
        />
        <p class="error" v-if="errors.birthday">{{ errors.birthday }}</p>

        <div class="tags">
          <label for="email">メールアドレス</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="email"
          v-model="form.email"
          type="text"
          placeholder="パソコン、携帯どちらも可"
        />
        <p class="error" v-if="errors.email">{{ errors.email }}</p>

        <div class="tags">
          <label for="thumbnaiil">アイコン画像</label>
          <span class="required">必須</span>
        </div>
        <div class="current-thumbnail">
          <span class="current-thumbnail-span">現在：</span>
          <img :src="form.thumbnail">
        </div>
        <MyFileInput2
          v-on:input="new_thumbnail = $event"
          v-bind:value="new_thumbnail"
        />

        <p class="error" v-if="errors.thumbnail">{{ errors.thumbnail }}</p>

        <div class="tags">
          <label for="clinic_name">医院名</label>
          <span class="required">必須</span>
        </div>

        <MyInput
          id = "clinic_name"
          v-model="form.clinic_name"
          :type="'text'"
          placeholder="例) オプテック歯科医院"
        />
        <p class="error" v-if="errors.clinic_name">{{ errors.clinic_name }}</p>

        <div class="tags">
          <label for="address">医院住所</label>
          <span class="required">必須</span>
        </div>

        <MyInput
          id = "address"
          v-model="form.address"
          :type="'text'"
          placeholder="例) 北海道札幌市厚別区厚別西２条3-7-15"
        />
        <p class="error" v-if="errors.address">{{ errors.address }}</p>
        <div class="tags">
          <label for="phone">連絡先電話番号</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="phone"
          v-model="form.phone"
          type="text"
          placeholder="固定電話、携帯どちらも可"
        />
        <p class="error" v-if="errors.phone">{{ errors.phone }}</p>

        <div class="tags">
          <label for="notification">受領通知</label>
          <span class="required">必須</span>
        </div>
        <div class="notification-wrapper">
          <MySelect
            id="notification"
            v-model="form.alert_freq"
            :options = "notifications"
          >
            <template slot-scope="props">
              <option v-for="(option, key) in props.options"
                  :key="key"
                  :value="option.id"
                  :selected="form.alert_freq === option.id"
              >{{ option.value }}
              </option>
            </template>
          </MySelect>
        </div>
        <p class="error" v-if="errors.notification">{{ errors.notification }}</p>

        <div class="spacer"></div>
        <MyButton
          caption="更新"
          bgcolor="#db362e"
          @click="post"
          :loading = buttonLoading
        />
        <router-link to="changePassword">
          <a>パスワード変更はこちら</a>
        </router-link>
      </div>
    </div>
  </div>
</div>
</template>
<style scoped>
  label {
    color: gray;
  }
  .my-container {
    padding: 10px 0;
    text-align: center;
    width: 100vw;
    background-color: #efefef
  }
  .logo {
    width: 100%;
  }
  .logo img {
    height: 100px;
  }
  .my-card {
    display: inline-block;
    width: 400px;
    border-radius: 10px;
    padding: 2rem;
    background-color: white;
    border: 1px solid rgba(0,0,0,.125)
  }
  .tags {
    text-align: left;
    margin-top: 20px;
  }
  .required {
    display: inline-block;
    padding: 2px;
    color: white;
    font-weight: bold;
    font-size: 12px;
    background-color: red;
    border-radius: 3px;
  }

  .notification-wrapper {
    margin: 1px 0;
  }
  .spacer {
    height: 10px;
  }
  p.error {
    background-color: red;
    color: white;
    border-radius: 3px;
    text-align: left;
    padding: 0 5px;
  }
  .current-thumbnail {
    display: flex;
    width: 100%;
    padding: 5px;
  }
  .current-thumbnail-span {
    display: inline-block;
  }
  .current-thumbnail img {
    display: inline-block;
    width: 30px;
    height: auto;
    border-radius: 50%;
  }
  @media screen and (max-width: 480px) {
  /* 480pxまでの幅の場合に適応される */
    .my-card {
      width: 350px
    }
  }
</style>
<script>
import Navbar from './Navbar'
import Modal from './components/Modal'
import MyInput from './components/MyInput'
import MyButton from './components/MyButton'
import MySelect from './components/MySelect'
import MyFileInput2 from './components/MyFileInput2'
import basemixin from './mixins/basemixin'
import UserProfileBirthDay from './components/UserProfileBirthDay'
export default {
  mixins: [basemixin],
  components: {
    Navbar,
    Modal,
    MyInput,
    MyButton,
    MySelect,
    MyFileInput2,
    UserProfileBirthDay
  },
  data () {
    return {
      isModified: false,
      buttonLoading: false,
      form: {},
      new_thumbnail: null,
      errors: {},
      modal: false
    }
  },
  watch: {
    'form.name': function (val) {
      this.errors.name = ''
    },
    'form.furigana': function (val) {
      this.errors.furigana = ''
    },
    'form.email': function (val) {
      this.errors.email = ''
    },
    'form.phone': function (val) {
      this.errors.phone = ''
    },
    'form.clinic_name': function (val) {
      this.errors.clinic_name = ''
    },
    'form.address': function (val) {
      this.errors.address = ''
    },
    'form.alert_freq': function (val) {
      this.errors.alert_freq = ''
    },
    'new_thumbnail': function (val) {
    }
  },
  computed: {
    notifications: function () {
      return [
        { id: 0, value: '毎回通知' },
        { id: 1, value: '30毎' },
        { id: 2, value: '1時間' },
        { id: 3, value: '通知しない' }
      ]
    }
  },
  methods: {
    initializeErrors: function () {
      this.errors = {
        name: '',
        furigana: '',
        email: '',
        phone: '',
        clinic_name: '',
        address: '',
        alert_freq: ''
      }
    },
    validate: function () {
      let isValid = true
      if (!this.form.name || this.form.name === '') {
        this.errors.name = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.furigana || this.form.furigana === '') {
        this.errors.furigana = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.email || this.form.email === '') {
        this.errors.email = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.phone || this.form.phone === '') {
        this.errors.phone = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.clinic_name || this.form.clinic_name === '') {
        this.errors.clinic_name = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.address || this.form.address === '') {
        this.errors.address = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.alert_freq || this.form.alert_freq === '') {
        this.errors.alert_freq = 'この項目は必須です'
        isValid = false
      }
      return isValid
    },
    async post () {
      this.initializeErrors()
      if (!this.validate()) {
        window.scrollTo(0, 0)
        this.modal = true
        return false
      }
      this.buttonLoading = true
      try {
        let formData = new FormData()
        for (const key in this.form) {
          if (key === 'thumbnail') {
            continue
          } else {
            formData.append(key, this.form[key])
          }
        }
        formData.append('thumbnail', this.new_thumbnail)

        await this.$store.dispatch(
          'auth/update',
          formData
        )

        window.scrollTo(0, 0)
        let message = '更新が完了しました。'
        this.flash(message, 'success', {
          timeout: 3000
        })
      } catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = 'ログインに失敗しました'
          console.log(data)
        } else {
          message = '予期しないエラーが起きました' + data
        }
        this.flash(message, 'error', {
          timeout: 3000
        })
      }
      this.buttonLoading = false
      this.isModified = false
    },
    preventRefresh (event) {
      event.preventDefault()
      event.returnValue = ''
    }
  },
  created () {
    this.form = JSON.parse(JSON.stringify(this.userInfo)) // deep copy
    this.initializeErrors()
    // this.form.new_thumbnail = null
  },
  beforeMount () {
    window.addEventListener('beforeunload', this.preventRefresh)
    this.$once('hook:beforeDestroy', () => {
      window.removeEventListener('beforeunload', this.preventRefresh)
    })
  },
  beforeRouteLeave (to, from, next) {
    if (!window.confirm('ページを離れますか？')) {
      return
    }
    next()
  }
}
</script>



