<template>
  <div
    :class="{ wrapper: true, active: currentPage === 'file' }"
  >
    <vue-loaders v-show="loading" name="ball-pulse" color="orange" scale="1"></vue-loaders>
    <div v-show="!loading">
      <div
        class="title"
      >
        <img class="file-section" src="@/assets/images/FileSection.png">
      </div>
      <select v-model="fileType" class="filetype-dropdown">
        <option
          v-for="(item, index) in fileTypes"
          :value="item.value"
          :key="index"
        >
          {{ item.caption }}
        </option>
      </select>
      <a
        class="file-card"
        v-for="(file, id) in shownFiles"
        :key = id
        :href="hostName + file.file_url" :download="file.file_name"
      >
        <div class="image-wrapper">
          <img v-if="isImage(file.file_name)" :src="hostName + file.file_url">
          <img v-else src="@/assets/images/file.png">
        </div>
        <span class="filename">{{ file.file_name | shorten }}</span>
      </a>
      <span class="nofilefound" v-if="shownFiles.length === 0">該当するファイルがありません</span>
    </div>
  </div>
</template>
<style scoped>
  /* main panel */
  div.wrapper {
    width: 30vw;
    height: 100%;
    background-color: #efefef;
    overflow-x: scroll;
    overflow-y: scroll;
  }
  .title {

    width: 100%;
    padding: 10px;
    text-align: center;
    background-color: transparent;
    color: gray
  }
  .filetype-dropdown {
    width: 80%;
    border: lightgray 1px solid;
    padding-left: 10px;
    height: 30px;
    font-size: 14px;
    margin: 10px 0;
    color: gray;
    -webkit-appearance: menulist;
    /*webkit browsers */
    -moz-appearance: menulist;
    /*Firefox */
    appearance: menulist;
    /* modern browsers */
  }
  .filetype-dropdown option {
    background-color: transparent;
    padding: 3px 0;

  }
  .file-section {
    width : 20%;
  }
  a {
    text-decoration: none;
  }
  .file-card {
    display: flex;
    color:  gray;
    font-size:  12px;
    height: 50px;
    line-height: 50px;
    border-bottom: lightgray solid 1px;
    text-align: left;
    padding: 5px 20px;
  }
  .file-card:hover {
    background-color: white;
  }
  .image-wrapper {
    display: inline-block;
    text-align: center;
    width: 20%;
  }
  .image-wrapper > img {
    display:inline-block;
    max-height: 100%;
    max-width:auto;
    border-radius: 3px;
  }
  span {
    display: inline-block;
  }
  .nofilefound {
    font-size: 14px;
    color:gray
  }

  .filename {
    display: inline-block;
    line-height: 45px;
    padding: 0 20px;
    text-align:left;
  }
  @media screen and (max-width: 480px) {
    div.wrapper {
      display: none
    }
    div.wrapper.active {
      width: 100%;
      display: block;
    }
  }
</style>
<script>
import moment from 'moment'
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  components: {
  },
  props: {
  },
  data () {
    return {
      fileTypes: [
        { value: 'all', caption: '全てのファイルを表示', extension: '.+' },
        { value: 'pdf', caption: 'PDFファイル', extension: 'pdf' },
        { value: 'image', caption: '画像ファイル', extension: 'jpe?g|png' },
        { value: 'presentation', caption: 'プレゼンテーション', extension: 'pptx?' },
        { value: 'word', caption: 'ドキュメント', extension: 'docx?' },
        { value: 'spreadsheet', caption: 'スプレッドシート', extension: 'xls(x|m)?' }
      ],
      fileType: 'all'
    }
  },
  mounted () {

  },
  computed: {
    shownFiles: function () {
      console.log(this.files)
      let fileType = this.fileTypes.filter((item) => {
        return item.value === this.fileType
      })
      if (fileType.length > 0) {
        fileType = fileType[0]
      } else {
        return false
      }
      const filtered = this.files.filter((file) => {
        var reg = RegExp(fileType.extension, 'g')
        const result = reg.test(this.getFileExtension(file.file_name))
        return result
      })
      console.log(filtered)
      return filtered
    }
  },
  methods: {
    getFileExtension: function (filename) {
      return filename.slice((filename.lastIndexOf('.') - 1 >>> 0) + 2)
    },
    isImage: function (val) {
      var globalRegex = RegExp('jpe?g|png', 'g')
      return globalRegex.test(val)
    }
  },
  filters: {
    toYYYYMMDD: function (data) {
      return moment(data).format('YYYY/MM/DD')
    },
    shorten: function (val) {
      let maxLength = 30
      if (val.length > maxLength) {
        return val.slice(0, maxLength) + '...'
      } else {
        return val
      }
    }
  }
}
</script>



